<template>
  <div>
    <div class="bkground">
      <section class="head searchHead clearfix">
        <div class="backWrap">
          <a @click="goBack" class="iconfont back">&#xe614;</a>
        </div>
        <div class="searchHeadRight">
          <div class="chartWrap">
            <!-- <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span> -->
        <a href="javascript:void(0)" @click="$router.push('/cart')" class="iconfont cart"> &#xe669; </a></div>
        </div>
      </section>
      <div class="shareWrap" >
        <div class="information">
          <div class="photo">
            <img src="../common/images/userHead.png">
          </div>
          <div class="name">
            <p>
              {{user.userName}}
              <a @click="goBack" class="iconfont back">&#xe654;</a>
            </p>
            <span>
              Invitation Code:
              <i>{{myStore.invitationCode}}</i>
            </span>
          </div>
          <div class="share">
            <img src="../common/images/share.png">
            <span>Share</span>
            <span class="iconfont blue">&#xe93d;</span>
          </div>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="totalRevenue">
        <p>
          {{myStore.totalRevenue||'0.00'}}
          <a @click="$router.push('comisson')" class="iconfont blue">&#xe93d;</a>
        </p>
        <span>Total revenue</span>
        <div class="balanceCash">
          <div class="balance">
            <p>{{myStore.balance||'0.00'}}</p>
            <span>Balance(AED)</span>
          </div>
          <div class="dividing">
            <div></div>
          </div>
          <div class="cash">
            <p>{{myStore.cash||'0.00'}}</p>
            <span>Cash(AED)</span>
            <span class="withdraw">Withdraw</span>
          </div>
        </div>
      </div>
      <div class="orderProfit">
        <div class="orders">
          <img src="../common/images/generalOrder.png">
          <div class="count">
            <p>{{myStore.orders||'0'}}</p>
            <span>Orders</span>
          </div>
        </div>
        <div class="dividing">
          <div></div>
        </div>
        <div class="yesterday">
          <img src="../common/images/yesterdayEarnings.png">
          <div class="count">
            <p>{{myStore.yesterdayRevenue||'0.00'}}</p>
            <span>Yesterday's Revenue</span>
          </div>
        </div>
      </div>
      <div class="customersInvited">
        <div class="myInvited">
          <div class="title">
            <span class="iconfont gray">&#xe658;</span>
            <p>Customers I Invited</p>
          </div>
          <div class="decollate">
            <img src="../common/images/division.png">
          </div>
        </div>
        <div class="invitedCount">
          <p>
            Yesterday Invited:
            <i>{{myStore.yesterdayInvited||'0'}}</i>
          </p>
          <p>
            Cumulative Invited:
            <i>{{myStore.cumulativeInvited||'0'}}</i>
          </p>
        </div>
      </div>
      <div class="decorative">
        <p>Decorative arts and crafts</p>
        <span>UP TO 60% OFF</span>
      </div>
    </div>
  </div>
</template>

<script>
import { myStore } from "@/api/store";
import { mapGetters } from "vuex";
export default {
  name: "myStores",
  data() {
    return {
        myStore:{}
    };
  },
  created() {
    myStore().then(res => {
      this.myStore=res.data.result;
    });

    console.log(this.user);
  },
  computed: {
    ...mapGetters(["user","indexData"])
  },
  methods: {
    confirm() {
      this.$dialog
        .confirm({
          message: "Confirm to Log Out?",
          confirmButtonText: "confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          this.$store.dispatch("logout");
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.bkground {
  background: url("../common/images/layer39.png") no-repeat;
  background-size: cover;
  width: 100%;
  height: 222px;
}
.backWrap {
  float: left;
  width: 25px;
  line-height: 42px;
}
.searchHeadRight {
  width: 35px;
  float: right;
  text-align: right;
  line-height: 42px;
}
.head {
  display: block;
}
.head .iconfont {
  color: white;
}
.shareWrap {
  width: 100%;
  padding-top: 22px;
  padding-left: 4%;
  overflow: hidden;
  .information {
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    .photo {
      margin-right: 10px;
      img {
        height: 58px;
        width: 58px;
      }
    }
    .name {
      p {
        color: #fff;
        font-size: 18px;
      }
      span {
        color: #fff;
        font-size: 12px;
      }
    }
    .share {
      position: absolute;
      height: 28px;
      right: -30px;
      img {
        height: 100%;
      }
      span {
        position: absolute;
        font-size: 14px;
        display: block;
        top: 5px;
        font-weight: 500;
        margin-left: 15px;
      }
      .iconfont {
        position: absolute;
        font-size: 12px;
        display: block;
        top: 9px;
        left: 60px;
        margin-left: 0;
      }
    }
  }
}
.center {
  width: 100%;
  background-color: #f6f6f6;
  position: relative;
  height: 500px;
  .totalRevenue {
    margin: 0 4%;
    background-color: #fff;
    width: 92%;
    position: absolute;
    top: -45px;
    border-radius: 5px;
    p {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 22px;
      color: #e33946;
      padding-top: 12px;
      a {
        font-size: 12px;
      }
    }
    span {
      font-size: 12px;
      color: #666666;
      display: block;
      text-align: center;
    }
  }
  .balanceCash {
    display: -webkit-box;
    padding-bottom: 15px;
    border-radius: 5px;
    .balance {
      width: 49%;
      p {
        font-size: 17px;
        color: #000;
      }
      span {
        font-size: 12px;
      }
    }
    .cash {
      width: 49%;
      p {
        font-size: 17px;
        color: #000;
      }
      span {
        font-size: 12px;
      }
      .withdraw {
        color: #1f89d3;
      }
    }
  }
  .orderProfit {
    background: url("../common/images/myStores.png") no-repeat;
    background-size: cover;
    width: 92%;
    margin: 0 4%;
    display: -webkit-box;
    position: absolute;
    top: 100px;
    .orders {
      width: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 30px 0;
      img {
        width: 29px;
        height: 29px;
      }
      .count {
        margin-left: 15px;
        p {
          color: #333333;
          font-size: 17px;
        }
        span {
          font-size: 12px;
          color: #666666;
        }
      }
    }
    .yesterday {
      width: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 29px;
        height: 29px;
      }
      .count {
        margin-left: 10px;
        p {
          color: #333333;
          font-size: 17px;
        }
        span {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }
  .customersInvited {
    width: 100%;
    height: 106px;
    position: absolute;
    background-color: #fff;
    top: 205px;
    display: -webkit-box;
    .myInvited {
      width: 50%;
      display: -webkit-box;
      .title {
        padding-left: 20px;
        padding-right: 5px;
        span {
          font-size: 23px;
          padding-top: 30px;
        }
        p {
          color: #333333;
          font-size: 14px;
          padding-top: 10px;
        }
        .iconfont {
          display: flex;
          justify-content: center;
        }
      }
      .decollate {
        img {
          height: 106px;
        }
      }
    }
    .invitedCount {
      width: 50%;
      p {
        color: #999999;
        font-size: 13px;
        padding-top: 22px;
        i {
          font-size: 15px;
          color: #666666;
        }
      }
    }
  }
  .decorative {
    width: 100%;
    height: 131px;
    background: url("../common/images/decorative.png") no-repeat;
    background-size: cover;
    position: absolute;
    top: 323px;
    p {
      width: 100%;
      font-size: 17px;
      color: #ffffff;
      text-align: center;
      padding-top: 30px;
    }
    span {
      width: 100%;
      font-size: 17px;
      color: #ffffff;
      text-align: center;
      display: block;
      padding-top: 10px;
    }
  }
}
.dividing {
  height: 66px;
  display: flex;
  align-items: flex-end;
  div {
    height: 62%;
    border-right: #cecece 1px solid;
  }
}
</style>



